// Uses CommonJS, AMD or browser globals to create a jQuery plugin.
define(['slick-carousel', 'jquery'], function (slick, $){
	$(document).ready(function (){
		$('.block-tree-slides').slick({
			dots: false,
			infinite: true,
			speed: 1500,
			slidesToShow: 1,
			autoplay: true,
			pauseOnHover: false,
			autoplaySpeed: 6000,
			adaptiveHeight: true
		});
		$('.block-tree-slides2').slick({
			dots: false,
			infinite: true,
			speed: 1500,
			slidesToShow: 1,
			autoplay: true,
			autoplaySpeed: 6000,
			adaptiveHeight: true
		});
		$('.block-sliderPersonal').slick({
			dots: false,
			infinite: true,
			speed: 700,
			slidesToShow: 1,
			autoplay: true,
			autoplaySpeed: 3000,
			adaptiveHeight: true,
			touchMove: false,
			responsive: [
				{
					breakpoint: 760,
					touchMove: true,
				}]
		});
		$('.block-slidetOtziv').slick({
			dots: false,
			infinite: true,
			speed: 700,
			slidesToShow: 1,
			autoplay: false,
			// autoplaySpeed: 3000,
			adaptiveHeight: true,
			touchMove: false,
			responsive: [
				{
					breakpoint: 760,
					touchMove: true,
				}]
		});
		$('.titleSlider').slick({
			dots: false,
			infinite: true,
			speed: 700,
			slidesToShow: 1,
			autoplay: true,
			autoplaySpeed: 3000,
			touchMove: false,
			adaptiveHeight: true
		});
		$('.firms').slick({
			dots: false,
			infinite: true,
			speed: 700,
			slidesToShow: 5,
			slidesToScroll: 5,
			autoplay: true,
			autoplaySpeed: 5000,
			touchMove: false,
			adaptiveHeight: false,
			asNavFor: '.firms',
			appendArrows: $('#firmsArrows'),
			responsive: [
				{
					breakpoint: 1350,
					settings: {
						slidesToScroll: 4,
						slidesToShow: 4
					}
				},
				{
					breakpoint: 1020,
					settings: {
						slidesToScroll: 3,
						slidesToShow: 3
					}
				},
				{
					breakpoint: 850,
					settings: {
						slidesToScroll: 1,
						slidesToShow: 1,
						centerMode: true,
						variableWidth: true
					}
				}]
		});

	});
});



